import React from 'react'
import { Link } from 'react-router-dom';
class Home extends React.Component {

    constructor(props) {
        super(props);
        this.state = {
            frameworks: [
                {
                name: 'Angular Learnings',
                description: 'For last 3 years, I have been working on various Angular Projects, I will be soon updating my learnings here.',
                myLevel: 'Expertise',
                url: '/topics'
                },
                {
                name: 'ReactJs Learnings',
                description: 'I am building this site in ReactJs, work is in progress.',
                myLevel: 'Intermidate',
                url: 'https://github.com/dheerajbharsiya?tab=repositories'
                },
                {
                name: 'Javascript Learnings',
                description: 'I had started using javascript for form validation in java projects.',
                myLevel: 'Expertise',
                url: 'https://github.com/dheerajbharsiya?tab=repositories'
                },
                {
                name: 'Vuejs Learnings',
                description: 'I have started to work on VueJs and I think it a is fast, simple, scalable library.',
                myLevel: 'Beginner',
                url: 'https://github.com/dheerajbharsiya?tab=repositories'
                }
            ]
        }
    }

  render() {
    return (
        <div>
        {this.state.frameworks.map((value, index) => {
            return (
                <div className="card" style={{width: '18rem'}} key={value.name}>
                    <div className="card-body">
                        <h5 className="card-title">{value.name}</h5>
                        <p className="card-text">{value.description}</p>
                        <p><Link to={value.url} className="btn btn-primary btn-bottom-aligned">{value.name}</Link></p>
                    </div>
                </div>
                )
            })
        }
        </div>
    )
  }
}
export default Home